
Template.__checkName("search");
Template["search"] = new Template("Template.search", (function() {
  var view = this;
  return Blaze.If(function() {
    return Spacebars.call(view.lookup("canSearch"));
  }, function() {
    return [ "\n    ", HTML.DIV({
      "class": function() {
        return [ "search ", Spacebars.mustache(view.lookup("searchQueryEmpty")), " ", Spacebars.mustache(view.lookup("moduleClass")) ];
      }
    }, "\n        ", HTML.INPUT({
      id: "search",
      type: "search",
      "class": "search-field",
      placeholder: function() {
        return Spacebars.mustache(view.lookup("_"), "rechercher...");
      },
      value: function() {
        return Spacebars.mustache(view.lookup("searchQuery"));
      }
    }), "\n    "), "\n  " ];
  });
}));
